import { startUpUrl } from '../../../utils'
import { BrandingUTM } from '../../../constants';
const CustomBranding = ({ appData }) => {
    let networks = appData.webFilters != '' && appData.webFilters ? appData.webFilters.map(({ networkId }) => networkId) : null;
    const startupDtls = startUpUrl(networks, appData.wallID);
    return <div
        ref={(el) => {}}
    >{
     
            startupDtls[1].map((item, index) => item.isAnchor ? <a key={index} href={startupDtls[0] + BrandingUTM} target="_blank" aria-label={item.text}
                ref={(el) => {
                    if (el) {
                        el.style.setProperty('align-items', 'center', 'important');
                        el.style.setProperty('display', 'flex', 'important');
                        el.style.setProperty('width', 'auto', 'important');
                        el.style.setProperty('text-decoration', 'none', 'important');
                        el.style.setProperty('padding', '8px', 'important');
                        el.style.setProperty('color', '#0b57d0', 'important');
                    }
                }}>
                {item.text} 
            </a> : item.text)
        }
    </div>
}

export default CustomBranding;